@import './src/common/styles/sass/variables';
@import './src/common/styles/sass/mixins';

.footer {
  background: $bgThirdColor;
}

.container {
  @include container();
  padding: 30px 0;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  color: $textColor;
}

.copyright {
  font-size: 14px;
  color: $textSecondColor;

  margin-top: 25px;
}

.socialsBlock {
  display: flex;
  justify-content: space-between;
  width: 350px;
}

@media screen and (max-width: 450px) {
  .socialsBlock {
    width: 95%;
  }
}